 -->
<template>
  <div class="malfunction">
    <title-header title="隐患通知单"></title-header>
    <div class="selects">
      <div class="select-titles">
        <el-row :gutter="24">
          <el-col :span="8"><div class="common-item">开始时间</div></el-col>
          <el-col :span="8"><div class="common-item">结束时间</div></el-col>
          <el-col :span="8"><div class="common-item"></div></el-col>
        </el-row>
      </div>
      <div class="select-content">
        <el-row :gutter="24">
          <el-col :span="8">
            <div class="common-item selsct-common">
              <el-date-picker
                v-model="queryFilter.startTime"
                type="datetime"
                placeholder="请选择">
              </el-date-picker>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="common-item selsct-common">
              <el-date-picker
                v-model="queryFilter.endTime"
                type="datetime"
                placeholder="请选择">
              </el-date-picker>
            </div>
          </el-col>
          <el-col :span="8">
            <el-button class="blue" type="primary" @click="handleSearch">查询</el-button>
          </el-col>
        </el-row>
      </div>
    </div>
    <div class="tool">
      <el-button class="opacity" :class="{blue: noticestatus === 0}" type="primary" @click="changenoticestatus(0)">未读</el-button>
      <el-button class="opacity" :class="{blue: noticestatus === 1}" type="primary" @click="changenoticestatus(1)">已读</el-button>
      <el-button class="opacity" :class="{blue: noticestatus === 2}" type="primary" @click="changenoticestatus(2)">全部</el-button>
    </div>
    <div class="data">
      <iot-table
        :tableHeader="tableHeader"
        :tableData="tableData"
        :pagination="pagination"
        :loading="loading"
        @reloaddata="initData"
      >
        <div slot="col-user_name" slot-scope="scope"><p>{{scope.row.user_name}} {{scope.row.user_phone}}</p></div>
        <div slot="col-alarm_type" slot-scope="scope"><p>{{alarmType(scope.row.alarm_type)}}</p></div>
        <div slot="col-notice" slot-scope="scope"><p>{{noticeTime(scope.row.notice)}}</p></div>
        <div slot="col-confirm" slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small">隐患通知单</el-button>
        </div>
      </iot-table>
    </div>
    <alarm-notice ref="alarmNotice" v-on:export-success="initData"></alarm-notice>
  </div>
</template>

<script>
import constant from '@/common/constant'
import { pageAllAlarms } from '@/api/hiddendanger'
import alarmNotice from './components/alarmNotice'
import dateUtil from '@/utils/dateUtil'
import iotTable from '@/components/iotTable'
import TitleHeader from '@/components/titleHeader'
import msgUtil from '@/utils/msgUtil'

export default {
  name: 'Malfunction',
  components: {
    alarmNotice,
    iotTable,
    TitleHeader
  },
  mounted() {
    this.initData()
  },
  data() {
    return {
      loading: false,
      queryFilter: {
        startTime: new Date(Date.now() - 7 * 24 * 60 * 60 * 1000),
        endTime: new Date()
      },
      project: {
        name: '',
        id: ''
      },
      noticestatus: 0,
      status: 0,
      tableData: [],
      pagination: {
        page: 1,
        per_page: 10,
        total: 0
      },
      tableHeader: [
        { prop: 'address', label: '安装位置' },
        { prop: 'raw_info.timestamp', label: '报警时间' },
        { prop: 'user_name', label: '监管人', type: 'slot' },
        { prop: 'place_name', label: '所属项目' },
        { prop: 'alarm_type', label: '事件类型', type: 'slot' },
        { prop: 'raw_info.mac', label: '设备mac' },
        { prop: 'notice', label: '通知时间', type: 'slot' },
        { prop: 'confirm', label: '操作', type: 'slot' }
      ]
    }
  },
  computed: {
    // 事件类型
    alarmType() {
      return (status) => {
        return constant.alarmTypes[status]
      }
    },
    // 通知时间
    noticeTime() {
      return (notice) => {
        const time = (notice || {}).create_time
        return time ? dateUtil.format(new Date(time * 1000), 'YYYY-MM-dd HH:mm:ss') : ''
      }
    }
  },
  methods: {
    changenoticestatus(status) {
      this.noticestatus = status || 0
      this.status = 0
      this.initData()
    },
    handleClick(row) {
      this.$refs.alarmNotice.show(row)
    },
    async initData() {
      this.loading = true
      const params = {
        page: this.pagination.page,
        per_page: this.pagination.per_page,
        start: this.queryFilter.startTime ? Math.ceil(this.queryFilter.startTime.getTime() / 1000) : '',
        end: this.queryFilter.endTime ? Math.ceil(this.queryFilter.endTime.getTime() / 1000) : '',
        notice_status: this.noticestatus
      }
      try {
        const { data } = await pageAllAlarms(params)
        this.tableData = data.items
        this.pagination = {
          total: data.page_info.total,
          page: data.page_info.page,
          per_page: data.page_info.per_page
        }
        this.loading = false
      } catch (error) {
        this.loading = false
        msgUtil.error('查询数据失败')
      }
    },
    handleSearch() {
      if (this.queryFilter.startTime !== '' || this.queryFilter.endTime !== '') {
        this.initData()
        return
      } else {
        msgUtil.error('查询条件不能为空')
      }
    }
  }
}
</script>

