  <div id="alarm_notice_container" v-show="showStatus">
    <div class="popup yh-detail" id="alarm_notice">
      <div style="background:#ffffff">
      <h1 align="center" style="color: red;font-size:24px">安全隐患通知单</h1>
      <h4 align="center">[{{pdfData.year}}] 编号 {{pdfData.number}}</h4>
      <hr align="center" color="red" size="2">
      <table width="100%"  class="">
        <tr>
          <td style="text-align:left">至 ：{{pdfData.place_name}}</td>
          <td style="text-align:right">{{pdfData.file_time}}</td>
        </tr>
      </table>
      <p style="text-indent:2em;">系统发现有告警长时间未有效处理，系统记录如下：</p>
      <p style="text-indent:2em;">隐患设备：{{pdfData.alarm_mac}}</p>
      <p style="text-indent:2em;">隐患位置：{{pdfData.street}}</p>
      <p style="text-indent:2em;">场所监管人：{{pdfData.supurvisor_list}}</p>
      <table  class=" text-center" style="text-indent:2em;" align="center" width="100%" cellspacing="15px">
        <tr>
          <th height="25px" align="center" class="text-center" style="font-weight: bold;">设备监管人</th>
          <th height="25px" align="center" class="text-center" style="font-weight: bold;">报警类型</th>
          <th height="25px" align="center" class="text-center" style="font-weight: bold;">时间</th>
        </tr>
        <tr v-for="(item, index) in pdfData.notice_time" :key="index">
          <td height="25px" align="center" class="text-center">{{pdfData.user_name}}</td>
          <td height="25px" align="center" class="text-center">{{pdfData.alarm_name}}</td>
          <td height="25px" align="center" class="text-center">{{item}}</td>
        </tr>
      </table>
      <p style="text-indent:2em;">为避免发生损失，【{{pdfData.province}}】特向您处发出火灾预警通知单，请及时消除安全隐患。</p>
      <img  class="stamp" :src="this.pdfData.stamp_url" />
      <table width="100%"  class="">
        <tr>
          <td style="text-align:right">（{{pdfData.province}}）科技有限公司</td>
        </tr>
        <tr>
          <td style="text-align:right"></td>
        </tr>
      </table>
      </div>
      <button class="btn btn-middle" id="btn-export" @click="exportPDF" v-auth="'NOTICE_E'">导出</button>
      <span class="layui-layer-setwin" id="btn-close">
        <a class="close-popup layui-layer-close layui-layer-close1" href="javascript:;" @click="cancel">
          <span class="iconfont iconguanbi close-iconfont gray"></span>
        </a>
      </span>
    </div>
  </div>
</template>
<script>
import { findPlaceDetail } from '@/api/hiddendanger'
import { downloadpdf, queryNoticeTime, findOne } from '@/api/alarm'
import msgUtil from '@/utils/msgUtil'
import constant from '@/common/constant'
import dateUtil from '@/utils/dateUtil'

export default {
  data() {
    return {
      showStatus: false,
      info: {
        fileNumber: '',
        sendTime: '',
        alarmType: '',
        address: '',
        deviceType: '',
        alarmTime: '',
        sendOrderTime: '',
        sendOrderUser: '',
        receiveOrderUser: '',
        receiveOrderUserPhone: '',
        handleTime: '',
        memo: '',
        projectName: '',
        admin: '',
        company: '',
        alarmId: ''
      },
      wmk: '',
      companyTitle: '',
      seal: '',
      originData: {},
      html: null,
      pdfData: {}
    }
  },
  methods: {
    show(data) {
      var dctime = data.raw_info.timestamp;
      dctime = dctime.substring(0, 19);
      dctime = dctime.replace(/-/g, '/');
      var timestamp = new Date(dctime).getTime() / 1000;
      downloadpdf({
        alarm_mac: data.raw_info.mac,
        alarm_type: data.alarm_type,
        user_name: data.user_name,
        place_id: data.place_id,
        dc_time: timestamp
      }).then(resp => {
        this.html = resp.img_file
        this.pdfData = resp.data
      }, error => {
        msgUtil.error('获取失败', msgUtil.findErrorMsg(error))
      })
      this.showStatus = true
    },
    findCompany(cid) {
      findOne(cid).then(resp => {
        // 初始化数据
        const custom = resp.data.custom_dict
        this.companyTitle = custom ? custom.NotifyComTitle : resp.data.name
        this.wmk = custom ? custom.NotfiyFootprint : ''
        this.seal = custom ? constant.qiniu_occc_url + custom.NotifyStamp : ''
      }, error => {
        msgUtil.error('获取系统用户失败', msgUtil.findErrorMsg(error))
      })
    },
    queryExportTime() {
      return queryNoticeTime({
        alarm_id: this.info.alarmId
      }).then(resp => {
        this.info.sendTime = dateUtil.format(new Date(resp.data * 1000), 'YYYY-MM-dd')
        this.originData.notice = { create_time: resp.data }
        return resp.data
      }, error => {
        msgUtil.error('导出失败', msgUtil.findErrorMsg(error))
      })
    },
    generatePDF(updateData) {
      document.getElementById('alarm_notice').style.borderRadius = '0'
      document.getElementById('btn-export').style.display = 'none'
      document.getElementById('btn-close').style.display = 'none'
      html2canvas(document.getElementById('alarm_notice'), { useCORS: true }).then((canvas) => {
        var contentWidth = canvas.width;
        var contentHeight = canvas.height;
        // 一页pdf显示html页面生成的canvas高度;
        var pageHeight = contentWidth / 595.28 * 841.89;
        // console.log(pageHeight)
        // 未生成pdf的html页面高度
        var leftHeight = contentHeight;
        // console.log(leftHeight)
        document.getElementById('alarm_notice').style.background = '#fff'
        const imgData = canvas.toDataURL('image/png')
        const doc = new jsPDF({ background: '#fff' })
        this.wmk && this.addWatermark(doc, this.wmk)
        // 当内容未超过pdf一页显示的范围，无需分页
        if (leftHeight < pageHeight) {
          // console.log(doc)
          doc.addImage(imgData, 'png', 15, 0)
        } else {
          while (leftHeight > 0) {
            doc.addImage(imgData, 'png', 15, 0)
            leftHeight -= pageHeight;
            // position -= 841.89;
            // 避免添加空白页
            if (leftHeight > 0) {
              doc.addImage(imgData, 'png', 15, 0)
            }
          }
        }

        doc.save('安全隐患通知单.pdf')
        document.getElementById('alarm_notice').style.borderRadius = '6px'
        document.getElementById('btn-export').style.display = 'inline-block'
        document.getElementById('btn-close').style.display = 'inline-block'
        updateData && this.$emit('export-success')
      })
    },
    addWatermark(doc, text) {
      A4[595.28, 841.89]
      const cav = document.createElement('canvas')
      cav.style.width = '200px'
      cav.style.height = '200px'
      const ctx = cav.getContext('2d')
      ctx.font = 'italic small-caps bold 16px Verdana'
      ctx.fillStyle = '#cfd8dc'
      ctx.rotate(-Math.PI / 6)
      ctx.fillText(text, 25, 180)
      const png = cav.toDataURL('image/png')
      const maxWidth = 150
      const maxHeight = 260
      let widthStart = 5
      let heightStart = 0
      do {
        doc.addImage(png, 'png', widthStart, heightStart)
        widthStart += 50
        if (widthStart > maxWidth) {
          widthStart = 5
          heightStart += 50
        }
      } while (heightStart < maxHeight)
    },
    exportPDF() {
      this.generatePDF(true)
    },
    cancel() {
      this.showStatus = false
    },
    queryProject(id) {
      findPlaceDetail(id).then(resp => {
        this.info.projectName = resp.data.place.name
        this.info.company = resp.data.place.company
        this.info.admin = resp.data.supervisors.map(s => {
          return s.name
        }).join('、')
      }, error => {
        msgUtil.error('加载项目数据失败', msgUtil.findErrorMsg(error))
      })
    }
  }
}
</script>
